/* The actual timeline (the vertical ruler) */
.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

/* The actual timeline (the vertical ruler) */
.timeline::after {
  content: '\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|\A\00a0|';
  font-weight: bold;
  color: white;
  position: absolute;
  width: 12px;
  background-color: rgb(12, 12, 12);
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}

/* Container around content */
.timeline-container {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
}

/* The circles on the timeline */
.timeline-container::after {
  content: "\f024";
  color: white;
  text-shadow: 0 0 10px rgb(52, 52, 52), 0 0 10px rgb(52, 52, 52);
  font-family: FontAwesome;
  font-size: 3rem;
  position: absolute;
  width: 25px;
  height: 25px;
  right: -17px;
  /* background-color: gray; */
  /* border: 4px solid #FF9F55; */
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}

/* Place the container to the left */
.left {
  left: 0;
}

/* Place the container to the right */
.right {
  left: 50%;
}

/* Add arrows to the left container (pointing right) */
.left::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid rgb(223, 222, 222);
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent rgb(223, 222, 222);
}

/* Add arrows to the right container (pointing left) */
.right::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid rgb(223, 222, 222);
  border-width: 10px 10px 10px 0;
  border-color: transparent rgb(223, 222, 222) transparent transparent;
}

/* Fix the circle for containers on the right side */
.right::after {
  left: -16px;
}

/* The actual content */
.timeline-content {
  padding: 20px 30px;
  background-color: rgb(255, 252, 252);
  position: relative;
  border-radius: 6px;
  box-shadow: 0 0 10px rgb(52, 52, 52);
}

/* Media queries - Responsive timeline on screens less than 600px wide */
@media screen and (max-width: 600px) {
  /* Place the timelime to the left */
  .timeline::after {
  left: 31px;
  }
  
  /* Full-width containers */
  .timeline-container {
  width: 100%;
  padding-left: 70px;
  padding-right: 25px;
  }
  
  /* Make sure that all arrows are pointing leftwards */
  .timeline-container::before {
  left: 60px;
  border: medium solid gray;
  border-width: 10px 10px 10px 0;
  border-color: transparent gray transparent transparent;
  }

  /* Make sure all circles are at the same spot */
  .left::after, .right::after {
  left: 15px;
  }
  
  /* Make all right containers behave like the left ones */
  .right {
  left: 0%;
  }
}
